html, body {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
  background: #1a001f;
  color: #eee;
}
a { text-decoration: none; color: #8a2be2 }
header { background: #2c003e; padding: 40px; text-align: center }
header img { width: 80px }
header h1 { font-size: 2.5rem; margin-top: 20px; color: #8a2be2 }
nav ul { list-style: none; display: flex; justify-content: center; margin-top: 20px }
nav li { margin: 0 15px }
main { padding: 60px 20px; max-width: 800px; margin: 0 auto }
section { margin-bottom: 60px }
section h2 { font-size: 2rem; border-bottom: 2px solid #8a2be2; display: inline-block; margin-bottom: 20px }
.services-list { display: flex; justify-content: space-around; flex-wrap: wrap }
.services-list li { background: #8a2be2; padding: 20px; border-radius: 8px; margin: 10px; min-width: 150px; text-align: center }
form { display: flex; flex-direction: column }
input, textarea { background: #2c003e; border: none; border-radius: 4px; color: #eee; padding: 15px; margin-bottom: 20px; font-size: 1rem }
button { background: #8a2be2; border: none; border-radius: 4px; color: #1a001f; padding: 15px; font-size: 1rem; cursor: pointer; transition: background .3s }
button:hover { background: #a84aff }
#response { margin-top: 20px; text-align: center }
footer { background: #2c003e; color: #aaa; text-align: center; padding: 20px }
